body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-inherit: inherit;
  --font-suez-one: "Suez One";
  --font-lalezar: Lalezar;
  --font-bakbak-one: "Bakbak One";
  --font-signika-negative: "Signika Negative";
  --font-bungee: Bungee;
  --font-metal-mania: "Metal Mania";
  --font-signika-negative-sc: "Signika Negative SC";

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-4xl: 23px;
  --font-size-xl: 20px;
  --font-size-34xl: 53px;
  --font-size-16xl: 35px;
  --font-size-41xl: 60px;
  --font-size-17xl: 36px;
  --font-size-9xl: 28px;

  /* Colors */
  --color-white: #fff;
  --color-gainsboro-100: #d9d9d9;
  --color-black: #000;
  --color-gray-100: rgba(0, 0, 0, 0.62);
  --color-gray-200: rgba(0, 0, 0, 0.5);
}
